<template>
  <nav>
    <el-row>
      <el-col :span="18">
        <div class="navbar-header">
          <router-link to="/">网上书城</router-link>
        </div>
      </el-col>
      <el-col :span="6">
        <el-menu mode="horizontal">
          <el-submenu v-if="isLogin" index="1">
            <template slot="title">{{username}}</template>
            <el-menu-item index="1-1">
              <router-link to="/personal">个人中心</router-link>
            </el-menu-item>
            <el-menu-item index="1-2">
              <a @click="onLogout">退出登录</a>
            </el-menu-item>
          </el-submenu>
          <el-menu-item v-else index="1">
            <el-button><a @click="onLogin">login</a></el-button>
          </el-menu-item>
        </el-menu>
      </el-col>
    </el-row>
  </nav>
</template>
<script>
export default {
  name: 'TitleHeader',
  data () {
    return {
      username: '',
      isLogin: false
    }
  },
  mounted () {
    setTimeout(() => {
      const token = this.$keycloak.token
      console.log('token=' + token)
      if (token) {
        this.$keycloak.loadUserProfile().then((data) => {
          console.log(data)
          this.username = data.username
          this.isLogin = true
          window.sessionStorage.setItem('username', data.username)
        })
      }
    }, 500)
    const username = window.sessionStorage.getItem('username')
    console.log('username=' + username)
    if (username !== null) {
      this.username = username
      this.isLogin = true
    }
  },
  methods: {
    onLogin () {
      this.$keycloak.login()
    },
    onLogout () {
      this.$keycloak.logoutFn({
        redirectUri: '/'
      })
      window.sessionStorage.removeItem('username')
      // this.$router.push('/')
    }
  }
}
</script>
<style scoped>
.navbar-header{
  height: 50px;
  padding-top: 10px;
  font-size: 28px;
}
.el-menu{
  background-color: transparent;
}
</style>
