    <section>
      <el-carousel height="300px">
        <el-carousel-item v-for="item in newList" :key="item.id">
          <h3 class="small">
            <h1>{{item.title}}</h1>
            <p>{{item.mAbstract}}</p>
          </h3>
        </el-carousel-item>
      </el-carousel>
    </section>
    <main>
      <div v-for="book in bookList" :key="book.title">
        <book-item
          :title="book.title"
          :mAbstract="book.mabstract"
          :imagePath="book.imagePath"
        ></book-item>
