    BookItem
  },
  data () {
    return {
      newList: [
        {
          id: 0,
          title: '新书上架：算法导论',
          mAbstract: '《算法导论》自第一版出版以来，已经成为世界范围内广泛使用的大学教材和专业人员的标准参考手册。'
        },
        {
          id: 1,
          title: '新书上架：机器学习',
          mAbstract: '机器学习是计算机科学与人工智能的重要分支领域. 本书作为该领域的入门教材，在内容上尽可能涵盖机器学习基础知识的各方面。'
        }
      ],
      bookList: []
    }
  },
  mounted () {
    this.getBookList()
  },
  methods: {
    getBookList () {
      this.$http.get('/books').then(res => {
        this.bookList = res.data.data
      })
    }
  }
}
</script>
