import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios'
import keycloak from '@dsb-norge/vue-keycloak-js'

Vue.use(ElementUI)

Vue.config.productionTip = false

Vue.prototype.$http = axios
// 请求拦截器，在每次请求发起之前调用
axios.interceptors.request.use(config => {
  const token = Vue.prototype.$keycloak.token
  if (token) {
    config.headers.Authorization = 'Bearer ' + token
  }
  return config
})

Vue.use(keycloak, { // 第一种方法
  init: {
    onLoad: 'check-sso'
  },
  config: {
    url: 'http://localhost:8180/auth', // 这里需要写你链接的地址
    realm: 'product_service', // 链接的领域
    clientId: 'vue-front' // 链接的客户端,
  },
  logout: {
    redirectUri: 'http://' + window.location.host + '/'
  },
  onReady: (keycloak) => {
    // keycloak.loadUserProfile().then((data) => {
    //   console.log(data)
    // })
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
